import { Search } from 'lucide-react'
export default function SearchFilter() {
    return (
        <section className="bg-white py-6 shadow-md">
            <div className="max-w-6xl mx-auto px-4 grid grid-cols-1 md:grid-cols-4 gap-4">
                <input
                    type="date"
                    className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-purple-500"
                    placeholder="Select Date"
                />
                <div className='flex justify-center items-center relative'>
                    <input
                        type="text"
                        className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-purple-500"
                        placeholder="Search by University"
                    />
                    <Search className="w-5 absolute right-3 hoverEffect" />
                </div>
                <div className='flex justify-center items-center relative'>
                    <input
                        type="text"
                        className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-purple-500"
                        placeholder="Search Event"
                    />
                    <Search className="w-5 absolute right-3 hoverEffect" />
                </div>
                <button className="w-full bg-purple-600 hover:bg-purple-700 text-white rounded-md px-4 py-2 font-semibold transition"> Search </button> </div > </section >);
}
